<template>
  <div>
    <div>
      <el-upload
        class="upload-demo"
        action="/image/info"
        multiple
        :limit="1"
        :on-success="onSuccess"
        :before-upload="beforeUpload"
        :on-error="onError"
      >
        <el-button size="small" type="primary">点击上传</el-button>
        <template #tip>
          <div class="el-upload__tip">
            只能上传
            TIFF/JPEG/WEBP/HEIC文件，系统不存储任何文件，大文件处理会超时。
          </div>
        </template>
      </el-upload>
    </div>
    <br />
    <div>
      <el-card>
        <div v-if="!loading">
          <div v-show="imageInfo && longitude">
            <div id="container" style="width: 100%; height: 300px"></div>
          </div>
          <br />
          <el-descriptions v-if="imageInfo">
            <el-descriptions-item
              v-if="imageInfo && imageInfo['Image Model']"
              label="拍摄设备"
              >{{ imageInfo["Image Model"] }}</el-descriptions-item
            >
            <el-descriptions-item
              v-if="imageInfo && imageInfo['Image Software']"
              label="拍摄软件"
              >{{ imageInfo["Image Software"] }}</el-descriptions-item
            >
            <el-descriptions-item
              v-if="imageInfo && imageInfo['Image DateTime']"
              label="拍摄时间"
              >{{ imageInfo["Image DateTime"] }}</el-descriptions-item
            >
          </el-descriptions>
          <el-collapse v-if="imageInfo" v-model="activeNames">
            <el-collapse-item  title="图片包含信息" name="1">
              <el-descriptions >
                <el-descriptions-item
                  v-for="(value, key) in imageInfo"
                  :label="key"
                  :key="key"
                  >{{ value }}</el-descriptions-item
                >
              </el-descriptions>
            </el-collapse-item>
                   
          </el-collapse>
          
          <el-empty v-else description="暂无数据"></el-empty>
        </div>
        <div v-else>
          <el-skeleton animated />
        </div>
      </el-card>
    </div>
  </div>
</template>


<script>
import AMap from "AMap"; // 引入高德地图
export default {
  name: "Picture",
  data() {
    return {
      activeNames:'1',
      imageInfo: null,
      loading: false,
      longitude: null,
      latitude: null,
    };
  },
  watch: {
    imageInfo: function () {
      if (this.imageInfo && this.imageInfo["GPS GPSLatitude"]) {
        this.longitude = this.gps(this.imageInfo["GPS GPSLongitude"]);
        this.latitude = this.gps(this.imageInfo["GPS GPSLatitude"]);
        this.$nextTick(() => {
          this.init();
        });
      }else{
        this.longitude =undefined
        this.latitude =undefined
      }
    },
  },

  methods: {
    gps(s) {
      s = s.replace("[", "").replace("]", "");
      let bt = 1;
      let ret = 0;
      s.split(",").forEach((r) => {
        let val;
        try {
          val = eval(r);
        } catch (err) {
          val = 0;
        }
        ret = ret + val / bt;
        bt = bt * 60;
      });
      return ret;
    },
    init() {
      let map = new AMap.Map("container", {
        resizeEnable: true,
        zoom: 15,
      });
      var marker = new AMap.Marker({
        icon: "https://webapi.amap.com/theme/v1.3/markers/n/mark_b.png",
        position: [this.longitude, this.latitude], //位置
      });
      map.setCenter([this.longitude, this.latitude]);
      map.add(marker); 
    },
    onSuccess(response, f, fileList) {
      if (response.data["解析不正常"]) {
        this.imageInfo = undefined;
        this.$message.error({
          message: response.data["解析不正常"],
          type: "success",
        });
      } else {
        this.imageInfo = response.data;
        this.$message.success({
          message: "解析完成。",
          type: "success",
        });
      }
      this.loading = false;
      fileList.pop();
    },
    onError() {
      this.loading = false;
    },
    beforeUpload() {
      this.imageInfo = null;
      this.loading = true;
    },
  },
};
</script>

